/* Chat board */

.viewChatBoard {
    display: flex;
    flex: 1;
    flex-direction: column;
    width: 100%;
    position: relative;
    
    /* background-image: url("https://i.pinimg.com/474x/2e/2e/25/2e2e25c53d858f4af66151304fdb68b7.jpg"); */
 
    backface-visibility: hidden;
    border-left: 1px solid #ededed;
    /* border-right: 5px solid #DFA375; */
    max-height: 100vh;
    min-height: 100vh;
    /* margin-right: 20px; */
    border-radius: 10px;
    /* box-shadow: 0 5px 5px #808888; */
   
  
}


.headerChatBoard {
    border-bottom: 1px solid #e8e8e8;
    padding: 5px;
    display: flex;
    
    
    background-color: #ededed;
    
}

.textHeaderChatBoard {
    font-weight: bold;
    color: #203152;
    margin-left: 10px;
    
}

.viewListContentChat {
    display: flex;
    flex: 1;
    flex-direction: column;
    overflow-y: scroll;
    padding-top: 10px;
    padding-bottom: 20px;
}



.viewBottom {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height:100px;
   
    background-color: #f0f0f0;
}

.icOpenGallery {
    width: 30px;
    height: 30px;
    margin-left: 10px;
}

.viewInputGallery {
    opacity: 0;
    position: absolute;
    z-index: -1;
    left: 10px;
    width: 30px;
}

.icSend {
    width: 30px;
    height: 30px;
    margin-left: 5px;
    margin-right: 30px;
}

.viewInput {
    flex: 1;
    border-radius: 30px;
    padding-left: 10px;
    padding-right: 10px;
    border: 0px;
    height: 50px;
    margin-right: 10px;

}

.viewInput:focus {
    outline: 0;
}

input::placeholder {
    color: rgb(199, 199, 199);
}

